<template>
  <PageWrapper
    :detail="true"
    dense
    contentFullHeight
    fixedHeight
    contentClass="fix-detail-page"
    style="position: relative"
  >
    <div class="load">
      <a-spin :spinning="formState.spinning" tip="Loading...">
        <div class="process-box">
          <!-- 操作按钮-->
          <div class="header-box">
            <div class="header-left">物资</div>
            <div class="header-right">
              <a-button type="primary" @click="handleSave" :loading="isLoading">
                {{ t('common.saveText') }}</a-button
              >
            </div>
          </div>
          <div class="content">
            <a-form ref="formRef" :model="formState" labelAlign="right" autocomplete="off">
              <a-card title="基本信息" :bordered="false">
                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="stationName" label="电站名称" :labelCol="{ span: 6 }">
                      <a-input disabled v-model:value="formState.stationName" />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item name="periodName" label="期数" :labelCol="{ span: 6 }">
                      <a-input disabled v-model:value="formState.periodName" />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="status" label="设备状态" :labelCol="{ span: 6 }">
                      <DictSelectBox
                        :type="`enable_or_disable`"
                        v-model:value="formState.status"
                        style="width: 100%"
                        disabled
                      />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item name="code" label="设备编码" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.code" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item
                      name="materialName"
                      label="物料名称"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请选择物料' }]"
                    >
                      <a-input v-model:value="formState.materialName" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item name="materialWholeName" label="物料全称" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.materialWholeName" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="materialCode" label="物料编码" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.materialCode" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item
                      name="modelCode"
                      label="型号"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请输入型号' }]"
                    >
                      <a-input v-model:value="formState.modelCode" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item
                      label="生产厂家"
                      name="factoryName"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请选择生产厂家' }]"
                    >
                      <a-input v-model:value="formState.factoryName" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item
                      label="供应商"
                      name="supplierName"
                      :labelCol="{ span: 6 }"
                      :wrapperCol="7"
                      :rules="[{ required: true, message: '请选择供应商' }]"
                    >
                      <a-input v-model:value="formState.supplierName" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="productionNumber" label="出厂编号" :labelCol="{ span: 6 }">
                      <a-input
                        v-model:value="formState.productionNumber"
                        disabled
                        placeholder="请输入出厂编号"
                      />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item
                      label="出厂日期"
                      name="productionDate"
                      labelAlign="right"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请选择出厂日期' }]"
                    >
                      <a-input v-model:value="formState.productionDate" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item
                      label="投运日期"
                      name="usedDate"
                      labelAlign="right"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请选择投运日期' }]"
                    >
                      <a-input v-model:value="formState.usedDate" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item
                      label="质保截止日期"
                      name="warrantyDate"
                      labelAlign="right"
                      :labelCol="{ span: 6 }"
                      :rules="[{ required: true, message: '请选择质保截止日期' }]"
                    >
                      <a-input v-model:value="formState.warrantyDate" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="subarrayName" label="区域子阵" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.subarrayName" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item name="name" label="设备名称" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.name" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="qty" label="数量" :labelCol="{ span: 6 }">
                      <a-input v-model:value="formState.qty" disabled />
                    </a-form-item>
                  </a-col>
                  <a-col :span="12">
                    <a-form-item
                      label="计量单位"
                      name="unitId"
                      :labelCol="{ span: 6 }"
                      :wrapperCol="15"
                    >
                      <a-input v-model:value="formState.unitId" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="12">
                    <a-form-item name="deviceCode" label="关联设备" :labelCol="{ span: 6 }">
                      <a @click="forwardPage">{{ formState.deviceCode }}</a>
                    </a-form-item>
                  </a-col>
                </a-row>

                <a-row :gutter="24">
                  <a-col :span="24" class="textarea-item">
                    <a-form-item
                      label="备注"
                      name="remark"
                      labelAlign="right"
                      :labelCol="{ span: 3 }"
                    >
                      <a-textarea :rows="5" v-model:value="formState.remark" disabled />
                    </a-form-item>
                  </a-col>
                </a-row>
              </a-card>

              <a-card
                title="拆分列表"
                :bordered="false"
                class="split-content"
                style="height: 600px; min-height: 600px"
              >
                <BasicTable @register="registerTable" @resize-column="handleResizeColumn">
                  <template #bodyCell="{ column, record }">
                    <template v-if="column.dataIndex === 'name'">
                      <a-input v-model:value="record[column.dataIndex]" :maxlenth="50" />
                    </template>
                    <template v-if="column.dataIndex === 'modelCode'">
                      <a-input v-model:value="record[column.dataIndex]" :maxlenth="128" />
                    </template>
                    <template v-if="column.dataIndex === 'subarrayName'">
                      <SearchBox
                        v-model:value="record[column.dataIndex]"
                        :columns="searchBoxColumns"
                        :data="areasetData"
                        :vfield="'name'"
                        :pagination="true"
                        twidth="100%"
                        @change="
                          (id, item) => {
                            record.areaId = item.record.key;
                            record.areaName = item.record.name;
                            record.subarrayId = item.record.key;
                            record.subarrayName = item.record.name;
                          }
                        "
                      />
                    </template>

                    <template v-if="column.dataIndex === 'productionNumber'">
                      <a-input v-model:value="record[column.dataIndex]" :maxlenth="64" />
                    </template>

                    <template v-if="column.dataIndex === 'productionDate'">
                      <a-date-picker
                        v-model:value="record[column.dataIndex]"
                        style="min-width: 100%"
                        value-format="YYYY-MM-DD"
                      />
                    </template>

                    <template v-if="column.dataIndex === 'usedDate'">
                      <a-date-picker
                        v-model:value="record[column.dataIndex]"
                        style="min-width: 100%"
                        value-format="YYYY-MM-DD"
                      />
                    </template>
                  </template>
                </BasicTable>
              </a-card>
            </a-form>
          </div>
        </div>
      </a-spin>
    </div>
  </PageWrapper>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted } from 'vue';
  import { useRoute } from 'vue-router';
  import { assign } from 'lodash-es';
  import { useI18n } from '@/hooks/web/useI18n';
  import { closeCurrentTab, addTabPage } from '@/utils/route';

  import { BasicColumn } from '@/components/Table';
  import { BasicTable, useTable } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';

  import * as EquipmentApi from '@/api/po/equipment';
  import { MsgManager } from '/@/message/MsgManager';
  import { SysMessage } from '/@/hooks/web/useMessage';
  import { defHttp } from '/@/utils/http/axios';
  import SearchBox from '@/components/Framework/Combox/SearchBox.vue';

  defineOptions({ name: 'EquipmentCreate' });

  const { t } = useI18n();
  const { query } = useRoute();
  const isLoading = ref(false);
  const formRef = ref();
  const AreasetApi = '/baseset/areaset/get-list';
  const areasetData = ref([]);
  const searchBoxColumns = ref([
    { title: '编码', dataIndex: 'code', key: 'code', fixed: 'left', minWidth: 50 },
    { title: '公司名称', dataIndex: 'name', key: 'name', fixed: 'left', minWidth: 50 },
  ]);
  const formState = reactive({
    spinning: false,
    processInstanceId: '',
    id: '',
    stationOrganId: '',
    stationOrganName: '',
    stationId: '',
    stationName: '',
    periodId: '',
    periodName: '',

    status: '',
    code: '',
    name: '',

    materialId: '',
    materialCode: '',
    materialName: '',
    materialWholeName: '',
    materialCategoryCode: '',

    modelCode: '',
    factoryId: '',
    factoryName: '',

    supplierId: '',
    supplierName: '',

    productionNumber: '',

    productionDate: '',
    usedDate: '',
    warrantyDate: '',

    areaId: '',
    areaName: '',
    subarrayId: '',
    subarrayName: '',

    qty: '',
    unitId: '',
    unitName: '',
    remark: '',

    deviceCode: '',
  });

  const forwardPage = () => {
    addTabPage('/baseset/monitor/device', '设备设置', { deviceEsn: formState.deviceCode });
  };

  // 列表数据
  const getDataApi = (url, params) =>
    defHttp.get(
      {
        url: url,
        params,
      },
      { isOnlyResult: true },
    );

  const getSearchBoxData = (url, params, searchBoxData) => {
    getDataApi(url, params).then((res) => {
      searchBoxData.value.splice(0, searchBoxData.value.length);

      res.forEach((e) => {
        searchBoxData.value.push({ id: e.id, code: e.code, name: e.name });
      });
      const dataList = searchBoxData.value;
      searchBoxData.value = [];
      setTimeout(() => {
        searchBoxData.value = dataList;
      }, 100);
    });
  };

  const columns: BasicColumn[] = [
    {
      title: '编号',
      dataIndex: 'code',
      width: 120,
    },
    {
      title: '设备名称',
      dataIndex: 'name',
      width: 160,
      resizable: true,
    },
    {
      title: '型号',
      dataIndex: 'modelCode',
      width: 160,
      resizable: true,
    },
    {
      title: '数量',
      dataIndex: 'qty',
      width: 60,
    },
    {
      title: '区域子阵',
      dataIndex: 'subarrayName',
      width: 160,
      resizable: true,
    },
    {
      title: '出厂编号',
      dataIndex: 'productionNumber',
      width: 160,
      resizable: true,
    },
    {
      title: '出厂日期',
      dataIndex: 'productionDate',
      width: 100,
      resizable: true,
    },
    {
      title: '投运日期',
      dataIndex: 'usedDate',
      width: 100,
      resizable: true,
    },
  ];

  const checkedKeys = ref<Array<string | number>>([]);

  function onSelect(record, selected) {
    if (selected) {
      checkedKeys.value = [...checkedKeys.value, record.id];
    } else {
      checkedKeys.value = checkedKeys.value.filter((id) => id !== record.id);
    }
  }

  const [registerTable, { getDataSource }] = useTable({
    api: EquipmentApi.getEquipmentSplit,
    columns,
    useSearchForm: false,
    bordered: true,
    // rowKey: 'code',
    pagination: false,
    // rowSelection: {
    //   type: 'checkbox',
    //   selectedRowKeys: checkedKeys,
    //   onSelect: onSelect,
    // },
    beforeFetch(search) {
      search.id = query.id;
      return search;
    },
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    showIndexColumn: true,
    scroll: { y: 720 },
    maxHeight: 720,
  });

  function handleResizeColumn(w, col) {
    col.width = w;
  }

  const handleSave = () => {
    formRef.value
      .validate()
      .then(() => {
        const dataSource = getDataSource();
        for (let i = 0; i < dataSource.length; i++) {
          let row = dataSource[i];
          if (!row.name) {
            SysMessage.getInstance().warning('请输入编号 ' + row.code + ' 的设备名称。');
            return false;
          }
          if (!row.modelCode) {
            SysMessage.getInstance().warning('请输入编号 ' + row.code + ' 的型号。');
            return false;
          }
          if (!row.subarrayName) {
            SysMessage.getInstance().warning('请输入编号 ' + row.code + ' 的区域子阵。');
            return false;
          }
          // if(!row.productionNumber){
          //   SysMessage.getInstance().warning('请输入编号 '+row.code+' 的出厂编号。');
          //   return false;
          // }
          if (!row.productionDate) {
            SysMessage.getInstance().warning('请输入编号 ' + row.code + ' 的出厂日期。');
            return false;
          }
          if (!row.usedDate) {
            SysMessage.getInstance().warning('请输入编号 ' + row.code + ' 的投运日期。');
            return false;
          }
        }
        doSave();
      })
      .catch((error) => {
        console.log('onSubmit=》error', error);
      });
  };

  const doSave = async () => {
    formState.spinning = true;
    try {
      const formData = {};
      formData.id = formState.id;

      const createReqVOs = ref([]);

      let dataSource = getDataSource();
      dataSource.map((record = {}, index) => {
        createReqVOs.value.push({
          code: record.code,
          name: record.name,
          modelCode: record.modelCode,
          areaId: record.areaId,
          areaName: record.areaName,
          productionNumber: record.productionNumber,
          productionDate: record.productionDate,
          usedDate: record.usedDate,
        });
      });
      formData.createReqVOs = createReqVOs.value;

      await EquipmentApi.createEquipmentSplit(formData);

      SysMessage.getInstance().success('保存成功。');

      setTimeout(() => {
        MsgManager.getInstance().sendMsg('equipment-page', {});
        closeCurrentTab();
      }, 1000);
    } catch (error) {
      console.log('error', error);
    } finally {
      formState.spinning = false;
    }
  };

  onMounted(async () => {
    if (query.id) {
      EquipmentApi.getEquipment(query.id).then((res) => {
        assign(formState, res);

        const params = { stationId: formState.stationId, stationPeriodId: formState.periodId };
        getSearchBoxData(AreasetApi, params, areasetData);
      });
    }
  });
</script>
<style lang="less" scoped>
  .to-font {
    color: #1890ff;
    text-decoration: underline;
  }

  .fix-detail-page {
    :deep(&.vben-page-wrapper-content.detail-content) {
      margin: 0;
    }
  }

  :deep(.ant-form-horizontal) {
    height: calc(100vh + 575px);

    .split-content {
      height: 820px;

      .ant-card-body {
        height: 720px;
      }
    }
  }
</style>
